  import { getVerifyCode, register } from 'api/api'
  import md5 from 'md5'
  export default {
    data() {
      return {
        phone: '',
        show: true,
        count: '',
        code: '',
        password: '',
        timer: null,
      }
    },
    created() {
      this.$watch('phone', (newPhone) => {
        this.phone = newPhone
      })
      this.$watch('code', (newCode) => {
        this.code = newCode
      })
      this.$watch('password', (newPsd) => {
        this.password = newPsd
      })
    },
    methods: {
      cancel() {
        this.$router.back()
      },
      confirm() {
        let mobile = this.phone
        let verify_code = this.code
        let pwd = md5(this.password)
        let is_mobile = 1
        let encrypt = 1
        let avatar = ''
        register(mobile,verify_code,pwd,is_mobile,encrypt,avatar).then((res)=>{
          if(res.status === 1) {
            this.$router.push({
              path: 'login'
            })
          }
        })
      },
      getCode(){
      // 进行数据调试
      let mobile = this.phone
      getVerifyCode(mobile).then((res)=>{
        // console.log(res)
        if(res.status === 1) {
          this.$createToast({
						time: 0,
						type: 'correct',
						txt: '发送短信验证码成功'
      		})
        }
      })
      const TIME_COUNT = 60;
      if (!this.timer) {
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
                this.count--;
              } else {
                this.show = true;
                clearInterval(this.timer);
                this.timer = null;
              }
            }, 1000)
      }
      } 
    }
  }



